body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ant-layout-header{
  color: #61dafb;
}

.ant-layout-sider-below {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  height: 100%;
}

.ant-layout{
  height: 100%
}
.ant-layout-content {
  padding: 1vh;
}

#root {
  height: 100%;
}

.ant-input{
  width: 40%;
}

.messages {
  min-height: 30%;
}
#chat {
  width: 80% ;
  left: 10%;
}

#chatbox {
  min-height: 30vmax;
  max-height: 70vmax;
  overflow-y:auto;
}

li {
  display:inline;
  margin: 3vh;
}


